@import url("https://fonts.googleapis.com/css?family=Barlow+Condensed:400,500,600,600i,700,800,900|Josefin+Sans:400,600,600i,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=PT+Sans:wght@400;700&display=swap");

@import "imports";

h1,
h2,
h3,
h4,
h5 {
  font-family: $headerFont;
  margin: 0;
}
ul {
  padding: 0;
}
li {
  list-style: none;
}
.app {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  color: $navy;
  font-family: $textFont;
  font-size: 10px;
  box-sizing: border-box;
  @media screen and(max-width: 855px) {
    font-size: 8px;
  }
  @media screen and(max-width: 700px) {
    font-size: 7.5px;
  }
  @media screen and(max-width: 500px) {
    font-size: 6px;
  }
}
.header {
  height: 100px;
  background-color: $navy;
  width: 100%;
  font-size: 4em;
  font-weight: bold;

  @include allCenter;
  &__content {
    width: 90%;
    display: flex;
    justify-content: space-between;
  }
  &__logOut {
    min-width: 100px;
    width: 15%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.logo {
  width: 80%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__img {
    display: inline-block;
    width: 65px;
    height: 65px;
    margin: 0 10px;
    overflow: hidden;
  }
  &__png {
    display: block;
  }
}
.main {
  width: 100%;
  mim-height: 100vh;
  display: flex;
}

.navbar {
  display: flex;
  flex-direction: column;
  min-width: 75px;
  width: 15%;
  min-height: calc(100vh - 134px);
  height: 100%;
  background-color: $gray;
  padding: 0 1em;

  &__list {
    padding: 0 10px;
  }
  &__link {
    display: block;
    @include bounceIn;
    color: $navy;
    text-decoration: none;
    font-size: 3em;
    font-weight: bold;
    margin: 10px 0;
    width: 100%;
  }
}
.content {
  text-align: center;
  width: 90%;
  min-height: calc(100vh - 100px);

  background-color: $white;
}

.btn {
  height: 40px;
  width: 100px;
  border-radius: $bradius;
  color: $navy;
  font-weight: bold;
  background-color: $gray;
  border: 2px solid $navy;
  @include btns;
}
.btns {
  height: 35px;
  width: 60px;
  margin: 10px 0;
  font-size: 0.6rem;
}
.gray {
  color: $gray;
}
.red {
  color: $red;
}
.hidden {
  display: none;
}
.textS {
  display: block;
  font-size: 0.8rem;
  color: $red;
}

body::-webkit-scrollbar {
  width: 10px;
}

body {
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px #e4edf5;
  }
  &::-webkit-scrollbar-button {
    height: calc(100% - 20px);
  }
  &::-webkit-scrollbar-thumb {
    background-color: $red;
    outline: none;
    border-radius: 10px;
  }
}

div {
  &::-webkit-scrollbar {
    width: 7px;
    margin: 0 5px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px #e4edf5;
  }
  &::-webkit-scrollbar-button {
    height: calc(100% - 40px);
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 100px;
    background-color: $navy;
    outline: none;
  }
}
